 62.5% => 1rem = 10px;\n}\n\nhtml::-webkit-scrollbar {\n  width: 0;\n  /* height: 16px; */\n}\n\nbody {\n  font-family: Roboto, -apple-system, BlinkMacSystemFont, \"Segoe UI\", Oxygen,\n    Ubuntu, Cantarell, \"Open Sans\", \"Helvetica Neue\", sans-serif;\n}\na {\n  text-decoration: none;\n}\n\ncode,\npre {\n  max-width: 100%;\n}\n",".container {\n  max-width: 116.1rem;\n  margin: 0 auto;\n}\n.heading-1 {\n  font-family: var(--font-base);\n  font-style: normal;\n  font-weight: 400;\n  font-size: 1.90145rem;\n  line-height: 120%;\n  /* or 23px */\n  text-align: center;\n  color: var(--white-color);\n  border: 1.18841px solid var(--white-color);\n\n  \n}\n\n.heading-2 {\n  font-family: var(--font-base);\n  font-weight: 400;\n  font-size: 4.8rem;\n  line-height: 120%;\n  /* identical to box height, or 58px */\n}\n\n.heading-3 {\n  font-family: var(--font-base);\n  font-weight: 400;\n  font-size: 3.6rem;\n  line-height: 130%;\n  /* identical to box height, or 47px */\n}\n\n.subtitle {\n  font-family: var(--font-base);\n  font-weight: 400;\n  font-size: 2.4rem;\n  line-height: 120%;\n  /* identical to box height, or 29px */\n}\n\n.btn-text {\n  font-family: var(--font-base);\n  font-weight: 500;\n  font-size: 1.6rem;\n  line-height: 140%;\n  /* or 22px */\n  text-transform: uppercase;\n}\n\n.body-text {\n  font-family: var(--font-base);\n  font-weight: 400;\n  font-size: 1.6rem;\n  line-height: 140%;\n  /* or 22px */\n  &--link {\n    font-family: var(--font-base);\n    font-weight: 500;\n    font-size: 1.6rem;\n    line-height: 140%;\n    /* or 22px */\n  }\n  &--backstage {\n    font-family: var(--font-base);\n    font-style: normal;\n    font-weight: 400;\n    font-size: 1.6rem;\n    line-height: 130%;\n    /* or 21px */\n    color: var(--blackblue-color);\n  }\n  &--feed {\n    font-family: var(--font-base);\n    font-style: normal;\n    font-weight: 500;\n    font-size: 1.6rem;\n    line-height: 130%;\n    /* identical to box height, or 21px */\n    color: var(--blackblue-color);\n  }\n  &--user {\n    font-family: var(--font-base);\n    font-style: normal;\n    font-weight: 400;\n    font-size: 1.6rem;\n    line-height: 120%;\n\n    /* or 19px */\n  }\n}\n\n.small-paragraph {\n  font-family: var(--font-base);\n  font-weight: 300;\n  font-size: 1.5rem;\n  line-height: 120%;\n  /* identical to box height, or 18px */\n}\n\n.text-underline {\n  border-bottom: 1px solid var(--orange-colortwo);\n}\n\n.copyright-text {\n  font-family: var(--font-base);\n  font-style: normal;\n  font-weight: 100;\n  font-size: 16px;\n  line-height: 130%;\n\n  /* identical to box height, or 21px */\n\n  color: #ffffff;\n\n  &-1 {\n    margin-bottom: 1rem;\n  }\n}\n\n.number-text {\n  font-family: var(--font-base);\n  font-style: normal;\n  font-weight: 500;\n  font-size: 7.2rem;\n  line-height: 80%;\n  text-align: center;\n  text-transform: uppercase;\n\n  color: var(--color-black);\n}\n\n.money-text {\n  font-family: var(--font-base);\n  font-style: normal;\n  font-weight: 500;\n  font-size: 26px;\n  line-height: 140%;\n  /* or 36px */\n\n  text-transform: uppercase;\n\n  color: #333b41;\n}\n",".header {\n  height: 8rem;\n  margin: 0 auto;\n  background: var(--black-color);\n\n  display: flex;\n  align-items: center;\n  justify-content: center;\n\n  @media only screen and (max-width: $bp-large) {\n    padding: 0 30px 0 30px;\n\n    z-index: 1;\n    position: relative;\n  }\n  &__logo {\n    width: 8.2rem;\n    height: 5.2rem;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background: url(../assets/png/bamboo.png);\n\n    background-repeat: no-repeat;\n    background-position: bottom 0.7rem left 1rem;\n    @media only screen and (max-width: $bp-large-2) {\n      width: 10.2rem;\n    }\n  }\n  &__navigation {\n    width: 101.5rem;\n    margin-left: 6.3rem;\n    &__list {\n      gap: 5.8rem;\n      @media only screen and (max-width: $bp-large) {\n        gap: 4.8rem;\n      }\n    }\n  }\n  &__item-grow {\n    flex: 1;\n  }\n}\n.navigation__list.header__navigation__list:nth-child(3) {\n  flex: 1;\n}\n",".navigation {\n  &__list {\n    display: flex;\n    list-style: none;\n  }\n  &__link:link {\n    text-decoration: none;\n    font-size: 1.4rem;\n    font-weight: 500;\n  }\n  &__link:link,\n  &__link:visited {\n    color: white;\n    &:hover {\n      color: var(--orange-color);\n    }\n  }\n  &__link--active:link,\n  &__link--active:visited {\n    & > span {\n      cursor: default;\n      background: var(--orange-gradient);\n      -webkit-background-clip: text;\n      -webkit-text-fill-color: transparent;\n      background-clip: text;\n      text-fill-color: transparent;\n    }\n  }\n}\n",".footer {\n  display: grid;\n  grid-template-columns: 1fr minmax(min-content, 116rem) 1fr;\n  background-color: black;\n  @media only screen and (max-width: $bp-large) {\n    grid-template-columns: 1fr minmax(min-content, 93.9rem) 1fr;\n  }\n\n  @media only screen and (max-width: $bp-large-2) {\n    grid-template-columns: 4.3rem 93.9rem 1.8rem;\n  }\n  &__container {\n    grid-column: 2/3;\n    display: grid;\n    grid-template-columns: repeat(3, max-content);\n    grid-template-rows: repeat(3, max-content) 5rem;\n    @media only screen and (max-width: $bp-large) {\n    }\n  }\n  &__logo {\n    grid-column: 1/2;\n    grid-row: 1/2;\n    margin-top: 4.1rem;\n  }\n  &__button-box {\n    grid-column: 1/2;\n    grid-row: 2/3;\n  }\n  #footer__btn {\n    padding: 1.6rem 5rem;\n    margin-top: 3.6rem;\n  }\n  &__social-items {\n    grid-column: 1/2;\n    grid-row: 3/4;\n    margin-top: 4.4rem;\n    display: flex;\n    flex-direction: column;\n    gap: 1rem;\n  }\n  &__navigation {\n    grid-column: 2/3;\n    grid-row: 3/4;\n    align-self: end;\n    margin-left: 10.1rem;\n    margin-right: 3.2rem;\n    @media only screen and (max-width: $bp-large) {\n      grid-column: 1/3;\n      margin-left: 29rem;\n    }\n\n    &__list {\n      display: flex;\n      gap: 5.8rem;\n\n      @media only screen and (max